'use strict';

const {
  URLWithLegacySupport,
  format
} = require('./url');

module.exports = function (url) {
  let location = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : {};
  let protocolMap = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : {};
  let defaultProtocol = arguments.length > 3 ? arguments[3] : undefined;
  let protocol = location.protocol ? location.protocol.replace(':', '') : 'http'; // Check protocol map

  protocol = (protocolMap[protocol] || defaultProtocol || protocol) + ':';
  let urlParsed;

  try {
    urlParsed = new URLWithLegacySupport(url);
  } catch (err) {
    urlParsed = {};
  }

  const base = Object.assign({}, location, {
    protocol: protocol || urlParsed.protocol,
    host: location.host || urlParsed.host
  });
  return new URLWithLegacySupport(url, format(base)).toString();
};